.enableMap{
  /*opacity: 0;*/
  -webkit-animation-name: enableMap;
          animation-name: enableMap;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  /*animation-delay: .4s;*/
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-timing-function: cubic-bezier(1,1,1,1);
          animation-timing-function: cubic-bezier(1,1,1,1);
}

@-webkit-keyframes enableMap{
  0%{
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  50%{
    opacity: 1;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  80%{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100%{
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes enableMap{
  0%{
    opacity: 0;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  50%{
    opacity: 1;
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  80%{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  100%{
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

